/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body {
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: #FFFFFF;
  color: #000000; }

/*--------------------------------------------------------------
# Site
--------------------------------------------------------------*/
body {
  padding-top: 78px; }

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
#header {
  height: 78px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.9); }
  .main-menu-open #header {
    background-color: #FFFFFF; }
  #header > .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%; }
  #header .logo span {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    width: 1px;
    word-wrap: normal !important;
    /* Many screen reader and browser combinations announce broken words as they would appear visually. */ }
    #header .logo span:focus {
      border-radius: 3px;
      box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
      clip: auto !important;
      clip-path: none;
      display: block;
      font-weight: bold;
      height: auto;
      left: 5px;
      line-height: normal;
      padding: 15px 23px 14px;
      text-decoration: none;
      top: 5px;
      width: auto;
      z-index: 100000;
      /* Above WP toolbar. */ }
  #header .main-menu {
    background: #16194A;
    color: #FFFFFF;
    text-align: center;
    position: fixed;
    top: 78px;
    left: 100%;
    transition: transform .2s;
    width: 100%;
    height: calc(100vh - 78px);
    text-transform: uppercase; }
    .main-menu-open #header .main-menu {
      transform: translateX(-100%); }
    #header .main-menu a {
      display: block;
      padding: 20px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1); }
  #header .main-menu-toggle {
    width: 48px;
    height: 48px; }
    #header .main-menu-toggle span {
      border: 0;
      clip: rect(1px, 1px, 1px, 1px);
      clip-path: inset(50%);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute !important;
      width: 1px;
      word-wrap: normal !important;
      /* Many screen reader and browser combinations announce broken words as they would appear visually. */ }
      #header .main-menu-toggle span:focus {
        border-radius: 3px;
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
        clip: auto !important;
        clip-path: none;
        display: block;
        font-weight: bold;
        height: auto;
        left: 5px;
        line-height: normal;
        padding: 15px 23px 14px;
        text-decoration: none;
        top: 5px;
        width: auto;
        z-index: 100000;
        /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Container
--------------------------------------------------------------*/
.container {
  padding-left: 12px;
  padding-right: 12px;
  margin: 0 auto;
  max-width: 1200px; }

/*--------------------------------------------------------------
# Button
--------------------------------------------------------------*/
.button {
  display: inline-block;
  background-color: #EB5757;
  font-weight: 700;
  color: #FFFFFF;
  transition: background-color .2s;
  padding: 10px 20px;
  font-size: 18px;
  text-transform: capitalize; }
  .button:hover, .button:active, .button:focus {
    background-color: #e62929; }
